* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@media screen and (min-width: 601px) {
  .test {
    text-align: center;
    color: #5ad;
    font-size: 100px;
    font-weight: 500;
  }
}

@media screen and (max-width: 600px) {
  .test {
    text-align: center;
    color: #5ad;
    font-size: 75px;
    font-weight: 500;
  }
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}
